import React from 'react';
import { Helmet } from 'react-helmet';

import { css } from '@emotion/react';

import { Footer } from '../components/Footer';
import SiteNav from '../components/header/SiteNav';

import { Wrapper } from '../components/Wrapper';
import IndexLayout from '../layouts';
import {
  inner,
  outer,
  SiteDescription,
  SiteHeader,
  SiteHeaderContent,
  SiteMain,
  SiteTitle,
  SiteNavMain,
  SiteArchiveHeader,
  ResponsiveHeaderBackground,
  SiteHeaderBackground,
} from '../styles/shared';
import { NoImage, PostFull, PostFullHeader, PostFullTitle } from '../templates/post';
import { colors } from '../styles/colors';

const PageTemplate = css`
  .site-main {
    margin-top: 64px;
    padding-bottom: 4vw;
    background: #fff;
  }

  @media (prefers-color-scheme: dark) {
    .site-main {
      /* background: var(--darkmode); */
      background: ${colors.darkmode};
    }
  }
`;

const Findtag = () => {
  return (
    <IndexLayout>
      <Helmet>
        <title>Findtag</title>
      </Helmet>
      <Wrapper css={PageTemplate}>
        <header className="site-archive-header no-image" css={[SiteHeader, SiteArchiveHeader]}>
          <div css={[outer, SiteNavMain]}>
            <div css={inner}>
              <SiteNav isHome={false} />
            </div>
          </div>
        </header>
        <main id="site-main" className="site-main" css={[SiteMain, outer]}>
          <div css={inner}>
            <article className="post page" css={[PostFull, NoImage]}>
              <PostFullHeader className="post-full-header">
                <PostFullTitle className="post-full-title">Find Tag</PostFullTitle>
              </PostFullHeader>

              <ResponsiveHeaderBackground
                css={[outer, SiteHeaderBackground]}
                backgroundImage="img/author-cover.jpg"
                className="site-header-background"
              >
                <SiteHeaderContent css={inner} className="site-header-content">
                  <SiteTitle className="site-title"><a  href="https://https://junmock.github.io/tags/개발-지식">개발 지식</a></SiteTitle>
                  <SiteDescription className="site-description">
                    개발 하면서 필요한 다양한 지식들을 모아모아
                  </SiteDescription>
                </SiteHeaderContent>
              </ResponsiveHeaderBackground>
              <ResponsiveHeaderBackground
                css={[outer, SiteHeaderBackground]}
                backgroundImage="img/author-cover.jpg"
                className="site-header-background"
              >
                <SiteHeaderContent css={inner} className="site-header-content">
                  <SiteTitle className="site-title"><a  href="https://https://junmock.github.io/tags/자바-api">자바 API</a></SiteTitle>
                  <SiteDescription className="site-description">
                    개발 하면서 필요한 다양한 지식들을 모아모아
                  </SiteDescription>
                </SiteHeaderContent>
              </ResponsiveHeaderBackground>
              <ResponsiveHeaderBackground
                css={[outer, SiteHeaderBackground]}
                backgroundImage="img/author-cover.jpg"
                className="site-header-background"
              >
                <SiteHeaderContent css={inner} className="site-header-content">
                  <SiteTitle className="site-title"><a  href="https://https://junmock.github.io/tags/DB">DB</a></SiteTitle>
                  <SiteDescription className="site-description">
                    개발 하면서 필요한 다양한 지식들을 모아모아
                  </SiteDescription>
                </SiteHeaderContent>
              </ResponsiveHeaderBackground>
            </article>
          </div>
        </main>
        <Footer />
      </Wrapper>
    </IndexLayout>
  );
};

export default Findtag;
